.nav-logo {
    display: flex;
    justify-content: flex-start;
    padding: 15px;
    align-items: center; }

.logo-img {
    max-width: 20vw; }

.prod-navigation {
    display: flex;
    margin-top: 10vh;
    padding: 15px;
    align-items: baseline; }



.selection-flex {
    display: flex;
    justify-content: flex-start;
    width: 50%; }

.selection-title {
    opacity: 0.2;
    margin-right: 1vw;
    margin-bottom: 0; }

.black-background .selection-title {
    opacity: 0.2; }

.selection-title:hover {
    opacity: 1; }

.black-background .selection-title.active {
    opacity: 1; }

.active {
    opacity: 1; }

.filters span:hover {
    text-decoration: underline; }

.projects-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding: 0 15px;
    grid-gap: 5px;
    grid-auto-flow: dense; }

.projects-container img {
    width: 100%; }

.project-vid {
    display: none; }

.grid-item {
    position: relative;
    height: 30vh;
    object-fit: cover; }

.project-img, .project-vid {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%; }


.larger-placement {
    grid-column-end: span 2;
    grid-row-end: span 2;
    height: 61vh; }

.Fashion, .Commercial, .Films, .Music {
    opacity: 1; }
