body {
    font-family: sofia-pro, sans-serif;
    margin: 0; }

body, html {
    overflow-x: hidden; }

h1, .h1, h2, .h2 {
    font-weight: 600;
    letter-spacing: 1px; }

.black-background {
    background-color: black;
    color: white; }

.white-background, .white-background:after, .white-background:before {
    background-color: white !important;
    color: black; }

.border-white {
    border-color: white !important; }

a {
    color: inherit;
    text-decoration: none; }

a:hover {
    color: inherit; }
